import React, { useState, useEffect } from "react";
import API from "../../utils/API";
import Container from "../../components/Container";
import SearchForm from "../../components/SearchForm";
import SearchResults from "../../components/SearchResults";
import ArticleContext from "../../utils/ArticleContext";

function Search() {
  const [articleState, setArticleState] = useState({
image: "",
name: "Something",
phone: "",
email: "",
DOB: ""
  });
  const [search, setSearch] = useState("");
  const [error, setError] = useState("");

  // only need to set the document.title after the first render
  useEffect(() => {
    document.name = "EmployeeName";
  }, []);

  useEffect(() => {
   
    
    API.searchTerms()
      .then(res => {
        console.log(res.data.results)
        if (res.data.length === 0) {
          throw new Error("No results found.");
        }
        if (res.data.status === "error") {
          throw new Error(res.data.message);
        }
        setArticleState({
        image:res.data.results.picture.thumbnail,
        Name: res.data.results.name.first,
        phone:res.data.results.phone,
        email:res.data.results.email,
        dob: res.data.results.dob.date
        });
      })
      .catch((err) => console.log(err));
  }, []);

  const handleInputChange = event => {
    setSearch(event.target.value);
  };

  const handleFormSubmit = event => {
    event.preventDefault();
  };
  return (
    <ArticleContext.Provider value={articleState}>
    <div>
      <Container style={{ minHeight: "100vh" }}>
        <h1 className="text-center">Search your employee by name.</h1>
     
        <SearchForm
         handleInputChange={handleInputChange}
         search={search} />
        <SearchResults />
      </Container>
    </div>
    </ArticleContext.Provider>
  );
}

export default Search;
